import uniDrawer from "@/components/uni-drawer/uni-drawer.vue";
import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
import parseHtml from '../../static/js/parseHtml.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote
} = app;
let {
	log
} = console;
export default {
	components: {
		uniDrawer
	},
	data() {
		return {
              
			kind: 0,
			active: -1,
			activeTab: 0,
			loading: true,
			firstId: '',
			secondId: '',
			page: 1,
			num: 10,
			firstCate: [],
			secondCate: [],
			bitmap:true,
			list: [],
			config: [],
			cartware: [],
			arrObj: {},
			index: '',
			textInfo: ''
		};
	},
	methods: {
		closeCart(){
			this.$refs.addcart.onClose();
		},
		closeKey(){
			this.$refs.keyboard.cancel();
		},
		toParent(e) {
			let item = e.arrObj;
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp,
				item_id: item.id,
				attr_id: 0,
				item_num: e.val
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign
				},
				obj
			);
			rs.postRequests('changeNum', params, res => {
				let data = res.data;
				if (data.code == 200) {
					rs.Toast('加入购物车成功');
					this.list[this.index].cart_num = e.val;
				} else if (data.code == 407 || data.code == 406) {
					rs.Toast("购买数量不能超过活动数量")
				} else {
					rs.Toast(res.data.msg)
				}
			});
			this.$refs.popup.close();
		},
		mpItem() {
			this.list=[];
			this.bitmap=true;
			this.loading = true;
			this.page=1;
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp
			};
			let {
				firstId,
				secondId,
				page,
				num
			} = this;

			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign,
					firstId: firstId,
					secondId: secondId,
					page: 1,
					num: num
				},
				obj
			);
			rs.getRequests('mpItemList', params, res => {
				let data = res.data;
				if (data.code == 200) {
					if (!firstId) {
						firstId = data.data.firstCate[0].id;
						
					}
					if (firstId) {
						data.data.firstCate.map((e, index) => {
							if (firstId == e.id) {
								this.activeTab = index;
							}
						});
					}
					this.config = data.data;
					this.firstCate = data.data.firstCate;
					this.secondCate = data.data.secondCate;
					this.list = data.data.list;
				
					if (data.data.list.length) {
						this.loading = false;
						this.bitmap=true;
						if (this.kind == this.secondCate.length - 1) {
							this.textInfo = parseHtml('没有更多呢');
						} else {
							this.textInfo = parseHtml("上滑或点击<span class='red_font'>" + this.secondCate[1].name + '</span>进入下一分类');
						}

					} else {
						this.bitmap=false;
						this.loading = false;
					}
				}
			});
		},
		// 切换一级分类
		changeFirst(index) {
			this.firstId = this.firstCate[index].id;
			this.secondId = "";
			this.kind = 0;
			getApp().globalData.classId = "";
			this.mpItem();
		},
		// 切换二级分类
		changeSecond(index) {
		
			this.secondId = this.secondCate[index].id;
			this.kind = index;
			this.mpItem();
		},
		nextSecond() {
			if (this.textInfo != '没有更多呢') {
				this.secondId = this.secondCate[this.kind + 1].id;
				this.kind += 1;
				this.mpItem();
			}
		},
		openCart(item) {
			this.cartware = item;
			this.$refs.cart.open();
		},
		onClose() {
			this.$refs.cart.close();
		},
		// 显示键盘
		showKey(item, index) {
			console.log(45)
			this.arrObj = item;
			this.index = index;
			this.$refs.popup.open();
		},
		showDraw() {
			this.$refs.drawer.open()
		},
		selectSort(index) {
			this.active = index;
		},
		cancelSort() {
			this.active = -1;
			this.$refs.drawer.close();
		},
		deterSort() {
			if (this.active == -1) {
				rs.Toast('请先选择分类')
				return;
			}
			this.firstId = this.firstCate[this.active].id;
			this.activeTab = this.active;
			this.secondId = "";
			this.kind = 0;
			this.mpItem();
			this.cancelSort();
		}
	},
	onShow() {
		let classId = getApp().globalData.classId;
		if (app.isReload == true) {
			this.kind = 0;
			this.active = -1;
			this.activeTab = 0;
			this.loading = true;
			this.firstId = '';
			this.secondId = '';
			this.page = 1;
			this.num = 10;
			if (classId) {
				this.firstId = classId
			}
			this.mpItem();
		} else {
			this.list = uni.getStorageSync('classify');
		}
	},
	onHide() {
		uni.setStorageSync('classify', this.list);
	},
	onReachBottom() {
		var that = this;
		var timeStamp = Math.round(new Date().getTime() / 1000);
		var {
			num,
			page,
			list,
			secondId,
			firstId
		} = that;
		var obj = {
			appid: appid,
			timeStamp: timeStamp
		};
		var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
		var data = {
			appid: appid,
			num: num,
			page: page + 1,
			firstId: firstId,
			secondId: secondId,
			timeStamp: timeStamp,
			sign: sign
		};
		this.loading = true;
		rs.getRequests('mpItemList', data, res => {
			if (res.data.code == 200) {
				if (res.data.data.list.length != 0) {
					this.list.push(...res.data.data.list);
					this.page += 1;
					this.loading = true;
				} else {
					this.loading = false;
					if (this.kind == this.secondCate.length - 1) {
						this.textInfo = parseHtml("没有更多呢");
					} else {
						this.textInfo = parseHtml("上滑或点击<span class='red_font'>" + this.secondCate[this.kind + 1].name +
							'</span>进入下一分类');
					}
				}
			}
		})
	},
	onLoad(e) {
		uni.hideTabBar();
	}
};
